import { ColorTransformOptions, GLType } from '../options'

export default function colorTransformPerChannelTransferFn(fn: () => string, extended: boolean, options: ColorTransformOptions) {
  if (extended) {
    return `
      {
        ${options.type === GLType.kWebGPU ? 'var v: f32' : 'float v'} = abs(color.r);
        ${fn()}
        color.r = sign(color.r) * v;
      }
      {
        ${options.type === GLType.kWebGPU ? 'var v: f32' : 'float v'} = abs(color.g);
        ${fn()}
        color.g = sign(color.g) * v;
      }
      {
        ${options.type === GLType.kWebGPU ? 'var v: f32' : 'float v'} = abs(color.b);
        ${fn()}
        color.b = sign(color.b) * v;
      }
    `
  }
  else {
    return `
      {
        ${options.type === GLType.kWebGPU ? 'var v: f32' : 'float v'} = color.r;
        ${fn()}
        color.r = v;
      }
      {
        ${options.type === GLType.kWebGPU ? 'var v: f32' : 'float v'} = color.g;
        ${fn()}
        color.g = v;
      }
      {
        ${options.type === GLType.kWebGPU ? 'var v: f32' : 'float v'} = color.b;
        ${fn()}
        color.b = v;
      }
    `
  }
}
