export default function getMediaSource() {
  if (typeof MediaSource === 'function') {
    return MediaSource
  }
  else if (typeof ManagedMediaSource === 'function') {
    return ManagedMediaSource
  }
  else {
    throw new Error('not support mse')
  }
}
