export default function flipVertex(vertex: number[], flipHorizontal: boolean, flipVertical: boolean) {
  if (flipHorizontal) {
    let x = vertex[3]
    let y = vertex[4]
    vertex[3] = vertex[17]
    vertex[4] = vertex[18]
    vertex[17] = x
    vertex[18] = y

    x = vertex[10]
    y = vertex[11]
    vertex[10] = vertex[24]
    vertex[11] = vertex[25]
    vertex[24] = x
    vertex[25] = y
  }
  if (flipVertical) {
    let x = vertex[3]
    let y = vertex[4]
    vertex[3] = vertex[10]
    vertex[4] = vertex[11]
    vertex[10] = x
    vertex[11] = y

    x = vertex[17]
    y = vertex[18]
    vertex[17] = vertex[24]
    vertex[18] = vertex[25]
    vertex[24] = x
    vertex[25] = y
  }
}
