import IPCPort from 'common/network/IPCPort'
import { Data } from 'common/types/type'

export default abstract class AudioWorkletProcessorBase extends AudioWorkletProcessor {

  protected ipcPort: IPCPort

  constructor() {
    super()
    this.ipcPort = new IPCPort(this.port)
  }

  public async request(method: string, params?: Data, transfer?: any[]) {
    return this.ipcPort.request(method, params, transfer)
  }

  public notify(method: string, params?: Data, transfer?: any[]) {
    this.ipcPort.notify(method, params, transfer)
  }
}
