export default function kid2Base64(id: Uint8Array) {
  let kid = ''
  id.forEach((a) => {
    kid += String.fromCharCode(a)
  })
  kid = btoa(kid)
  return kid.replace(/=/g, '')
    .replace(/\//g, '_')
    .replace(/\+/g, '-')
}
